<template>
    <div class="hello">
        {{msg}}
    </div>
</template>

<script>
export default {
    name: "index",
    data() {
        return {
            msg: "Hello, the world"
        };
    },
    created() {
        this.$alert('我是内容', '我是标题').then(() => {
            console.log('点击确定');
        }).catch(() => {
            console.log('点击取消');
        });
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style src="../style/index.css" scoped></style>



