import Vue from 'vue'
import {
	MessageBox,
	Dialog,
	// Message
} from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(MessageBox);
Vue.use(Dialog);
// Vue.use(Message);


Vue.prototype.$alert = (content, title, opt) => {
	let def = {
        center: true,
		showClose: false,   // 是否显示右上角X
        showCancelButton: false,    //是否显示取消按钮
        closeOnClickModal: false,   //点击遮罩层是否关闭
		confirmButtonText: '确定',
	};
	let settings = Object.assign({}, def, opt);
	return MessageBox.confirm(content, title, settings);
};
Vue.prototype.$confirm = (content, title, opt) => {
	let def = {
        center: true,
		showClose: false,
        closeOnClickModal: false,
	};
	let settings = Object.assign({}, def, opt);
	return MessageBox.confirm(content, title, settings);
};


